import { fromJS } from 'immutable';
import * as constants from './constants';

const defaultState = fromJS({
	user:[],
	usergusLike:[],
	userrecentOrder: [],
	allOrder:[]
});
const getPersonData = (state, action) => {
	

};
export default (state = defaultState, action) => {
	switch(action.type) {
		case constants.GET_PERSONDATA:
			return (
				state.set("user",action.user),
				state.set("usergusLike",action.usergusLike),
				state.set("allOrder",action.allOrder)
				)
		case constants.CHANGE_PERSON:
			return state.set("user",action.user)
		case constants.GET_ALL_ORDER:
			return state.set("allOrder",action.value)
		default:
			return state;
	}
}